import Cookies from 'js-cookie'
// 登陆token名称
export const TokenKey = 'financial-settlement-token'
// 登陆信息对象名称
export const storageKey = 'financial-user'
// store防止页面刷新数据丢失存储对象=>名称
export const storeName = 'userMsg'
// 记住密码=>名称
export const template_admin_userName = 'template_admin_userName'
export const template_admin_userPwd = 'template_admin_userPwd'
export const template_admin_checked = 'template_admin_checked'

// ---------

const storage = JSON.parse(localStorage.getItem(storageKey))

export function getToken() {
  return Cookies.get(TokenKey)
}

export function setToken(token) {
  return Cookies.set(TokenKey, token)
}

export function removeToken() {
  return Cookies.remove(TokenKey)
}
// 获取个人用户信息
export function getUserInfo() {
  const storage = JSON.parse(localStorage.getItem(storageKey))
  if (storage) {
    return storage.user
  }
}
// 获取个人菜单权限
export function getMenuCode() {
  const storage = JSON.parse(localStorage.getItem(storageKey))
  if (storage) {
    return storage.menuCodeList
  }
}
// 判断是否有操作权限
export function havePermission(code) {
  if (!code) {
    return true
  }
  if (storage) {
    const codeList = storage.buttonCodeList
    const isExist = codeList.some(item => {
      if (item === code) {
        return true
      }
    })
    return isExist
  }
  return false
}