import Vue from 'vue'
import VueRouter from 'vue-router'
import guaniu from '../views/guaniu'
import qiangGou from '../views/qiangGou'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'guaniu',
    component: guaniu
  },
  {
    path: '/qg',
    name: 'qiangGou',
    component: qiangGou
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
