            <div><span style="font-size: 18px;color: #333;">最高单价：</span><span  style="font-size: 18px;color: #333;font-weight: bold;">{{moneyForm.maxPrice}}/锭</span></div>
            <div><span style="font-size: 18px;color: #333;">剩余金额：</span><span  style="font-size: 18px;color: #333;font-weight: bold;">{{moneyForm.maxMoney}}元</span></div>
