        <div v-if="goodsData == null">
          <!-- <el-button :disabled="runing" @click="drawer = true" type="primary">设置参数</el-button> -->
          <el-button type="primary" @click="drawer = true" icon="el-icon-setting" circle></el-button>
        </div>

        <div v-show="goodsData != null && this.countdownTimer != null">
          <span style="margin:10px;font-size: 18px;color: #333;font-weight: bold;">公示期剩余时间：</span>
          <div class="time-item">
              <span id="day_show">0天</span>
              <strong id="hour_show">0时</strong>
              <strong id="minute_show">0分</strong>
              <strong id="second_show">0秒</strong>
