                  <dl class="selling_dl">
                      <dt class="goods_name ">{{goodsData.itemName}}</dt>
                      <div class="goods_info">
                        <dd class="goods_id"><label>物品编号:</label>{{goodsData.id}}</dd>
                        <dd class="goods_unit_price"><label>单价:</label>{{goodsData.unitPrice != null ? goodsData.unitPrice.toFixed(2) : goodsData.price}} 元</dd>
                        <dd class="goods_amount"><label>数量:</label>{{goodsData.itemAmount > 1000000 ? (parseInt(goodsData.itemAmount * 0.000001) + '锭') : goodsData.itemAmount}}</dd>
                      </div>
                      <dd class="goods_price">¥{{goodsData.price}}</dd>
                  </dl>
              </div>
              <div v-if="goodsData.itemDesc != null && goodsData.itemDesc.length > 0" class="tooltip-content" v-html="goodsData.itemDesc"></div>
            </div>
        </div>
        <div class="sub-btn">
            <!-- <el-button v-if="runing == false"  @click="handleStart()" type="primary">{{runing ? '运行中' : '开始运行'}}</el-button> -->
            <el-button type="primary" v-if="runing == false"  @click="handleStart()" icon="el-icon-thumb" circle></el-button>
            <el-button v-if="runing" @click="handleEnd()" type="primary" icon="el-icon-switch-button" circle></el-button>
        </div>
    </div>

      <div v-if="runing" class="wrap">
        <div class="bg load-bg">
          <div class="loading">
            <span class="title">running</span>
            <span class="text">程序运行中...</span>
          </div>
        </div>
      </div>
    
    <div class="runing-div">
        <span style="margin-bottom:10px;font-size: 18px;color: #333;font-weight: bold;">运行日志：</span>
        <div ref="runingLog" class="runing-log" :class="[{'log-bg':runing}]">
            <!-- <span>开始运行...</span> -->
            <span v-html="runingMessage"></span>
        </div>
    </div>
    
    <el-drawer
    :with-header="false"
    :visible.sync="drawer"
    direction="ltr"
    size="40%">
        <div style="margin: 20px;">
            <el-tabs v-model="tabsActiveName" type="card" @tab-click="handleTabsClick">
                <el-tab-pane label="银子" name="money">银子</el-tab-pane>
                <el-tab-pane label="宝物" name="baowu">宝物</el-tab-pane>
            </el-tabs>
            <div v-show="tabsActiveName == 'money'" class="drawer-div">
                <el-form
                ref="moneyForm"
                label-position="top"
                label-width="120px"
                :rules="rules"
                :model="moneyForm"
                >
                    <el-form-item label="最高单价：" prop="maxPrice">
                        <el-input
                        v-model.trim="moneyForm.maxPrice"
                        placeholder="最高单价（请检查余额是否充足）"
                        />
                    </el-form-item>
                    <el-form-item label="使用金额：" prop="maxMoney">
                        <el-input
                        v-model.trim="moneyForm.maxMoney"
                        placeholder="使用金额（请检查余额是否充足）"
