                    </el-form-item>
                    <el-form-item label="运行频率（毫秒）：" prop="runningSize">
                        <el-input-number v-model="moneyForm.runningSize" :step="500" :min="1000" :max="600000"></el-input-number>
                    </el-form-item>
                    <el-form-item label="运行频率是否随机：" prop="isRandom">
