            </div>
            <div v-show="tabsActiveName == 'baowu'" class="drawer-div" style="height:600px; overflow-y: scroll;">
                <el-form
                ref="baowuForm"
                label-position="top"
                label-width="120px"
                :rules="rules"
                :model="baowuForm"
                >
                    <el-form-item label="宝物类型：" label-position="top" prop="baowuTypeList">
                        <el-checkbox-group v-model="baowuForm.baowuTypeList">
                            <el-checkbox label="seal_jin">金宝物</el-checkbox>
                            <el-checkbox label="seal_yu">玉宝物</el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="宝物属性：" prop="baowuTypeList">
                        <div><el-button @click="handleAddBaowuPro" icon="el-icon-plus" circle></el-button></div>
                        <div style="margin-top:10px" v-for="(proItem, proIndex) of baowuForm.baowuProArr" :key="proIndex">
                            <el-select style="width:50%" value-key="name" v-model="proItem.baowuPro" placeholder="请选择">
                                <el-option v-for="(item, index) of baowuDataArr" :key="index" :label="item.name + '+'" :value="item"></el-option>
                            </el-select>
                            <el-input style="width:35%; margin-left:2%" placeholder="请输入数值" v-model="proItem.baowuProText" class="input-with-select">
                                <el-button v-if="proItem.baowuPro && proItem.baowuPro.type == true" slot="append">%</el-button>
                            </el-input>
                            <a @click="removeBaowuPro(proIndex)" v-if="proIndex != 0"><span style="margin-left:1%;">删除</span></a>
                        </div>
                    </el-form-item>
                    <el-form-item label="最高单价：" prop="maxPrice">
                        <el-input
                        v-model.trim="baowuForm.maxPrice"
                        placeholder="最高单价（请检查余额是否充足）"
                        />
                    </el-form-item>
                    <el-form-item label="使用金额：" prop="maxMoney">
                        <el-input
                        v-model.trim="baowuForm.maxMoney"
                        placeholder="使用金额（请检查余额是否充足）"
                        />
                    </el-form-item>
                    <el-form-item label="运行频率（毫秒）：" prop="runningSize">
                        <el-input-number v-model="baowuForm.runningSize" :step="500" :min="1000" :max="600000"></el-input-number>
                    </el-form-item>
                    <el-form-item label="运行频率是否随机：" prop="isRandom">
                        <el-switch
                            v-model="ruleForm.isRandom"
                            active-text="开"
                            inactive-text="关">
                        </el-switch>
                    </el-form-item>
                </el-form>
                
            </div>
        </div>
    </el-drawer>
  </div>
</template>
<script>
  // import md5 from "js-md5"
import Cookies from 'js-cookie'
import { setToken } from '../utils/auth'
import axios from 'axios'
import request from '../utils/urls'
import $ from 'jquery'
export default {
  name: 'Login',
  data() {
      return {
        drawer: false,
        direction: 'rtl',
        runing: false,
        itemId: null,
        orderId: null,
        runingMessage: '',
        timer: null,
        countdownTimer: null,
        goodsData: null,
        nowRunningSize: 10000, // 当前的运行频率
        tabsActiveName:'money',
        baowuDataArr:[
            {name:'外功暴击伤害', type:true},
            {name:'外功暴击率', type:true},
            {name:'外功伤害减免忽视', type:true},
            {name:'内功暴击伤害', type:true},
            {name:'内功暴击率', type:true},
            {name:'闪避率', type:true},
            {name:'暴击伤害减免', type:true},
            {name:'近身威力', type:false},
            {name:'内功威力', type:false},
            {name:'血气上限', type:false},
            {name:'柔防御忽视', type:false},
            {name:'阴防御忽视', type:false},
            {name:'阳防御忽视', type:false},
            {name:'刚防御忽视', type:false},
            {name:'柔伤害减免', type:true},
            {name:'阴伤害减免', type:true},
            {name:'阳伤害减免', type:true},
            {name:'刚伤害减免', type:true}],
        baowuForm: {
            baowuTypeList: ['seal_yu'],
            maxPrice: '',
            maxMoney: '',
            baowuProArr: [{baowuPro: null, baowuProText: ''}],
            runningSize: 60000
        },
        moneyForm: {
            maxPrice: '',
            maxMoney: '',
            runningSize: 10000
        },
        ruleForm: {
            loginCookie: '', 
            roleId: '',
            isRandom: true,
            runningSize: 10000
        },
        rules: {
          loginCookie: [
            { required: true, message: '请输入Cookie', trigger: 'change' }
          ],
          maxPrice: [
            { required: true, message: '请输入最高单价', trigger: 'change' },
            { min: 1, max: 6, message: '价格最大支持6个字符长度', trigger: 'change' },
            {
              pattern: /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/,
              message: '请输入正确的金额'
            }
          ],
          maxMoney: [
            { required: true, message: '请输入最大使用金额', trigger: 'change' },
            { min: 1, max: 6, message: '金额最大支持6个字符长度', trigger: 'change' },
            {
              pattern: /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/,
              message: '请输入正确的金额'
            }
          ]
        },
        unitArr:[   // 个位
            'nh',
            'nl',
            'np',
            'nt',
            'jF',
            'jJ',
            'jN',
            'jR',
            'jV',
            'jZ'
        ],
        tenArr:[  // 十位
            'B',
            'F',
            'J',
            'N',
            'R',
            'V',
            'Z',
            'j',
            'n',
            'r'
        ],
        qianArr:[  // 千位
            '4',
            '5',
            '6',
            '7',
            'x',
            'y',
            'z',
            '0',
            '1',
            '2'
        ],
        wanArr:[  // 万位
            'QX',
            'RX',
            'SX',
            'TX',
            'UX',
            'VX',
            'WX',
            'XX',
            'YX',
            'a2'
        ],
        baiArr23:[  // 百位
            'RU1',
            'VU1',
            'a01',
            'e01',
            'RU5',
            'VU5',
            'a05',
            'e05',
            'RU9',
            'VU9'
        ],
        baiArr45:[  // 百位
            'RU0',
            'VU0',
            'a00',
            'e00',
            'RU4',
            'VU4',
            'a04',
            'e04',
            'RU8',
            'VU8'
        ]
      }
    },
  watch:{
    drawer(val) {
      if (this.drawer == false) {
        if (this.tabsActiveName == 'money') {
            this.$refs['moneyForm'].validate((valid) => {
                if (valid) {
                    this.putawayAllGoodsInfo('money')
                }else {
                    this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 金额不合法，请重新输入')
                    this.goodsData = null
                }
            })
            this.countdownTimerEnd()
        }else if (this.tabsActiveName == 'baowu') {
            this.$refs['baowuForm'].validate((valid) => {
                if (valid) {
                    this.expectAllGoodsInfo('baowu')
                }else {
                    this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 输入内容不合法，请重新输入✍️')
                    this.goodsData = null
                }
            })
            this.countdownTimerEnd()
        }
      }
    }
  },
  mounted() {
    document.addEventListener('keydown', this.keydownListener)
    this.drawer = true
    this.messageScollrBottom('[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 欢迎使用～～👏👏👏')
  },
  beforeDestroy() {
    document.removeEventListener('keydown', this.keydownListener)
  },
  methods: {
      /** ********宝物类型相关方法******** */ 
      // 添加属性
    handleAddBaowuPro() {
        this.baowuForm.baowuProArr.push({baowuPro: null, baowuProText: ''})
    },
    // 减属性
    removeBaowuPro(proIndex) {
        this.baowuForm.baowuProArr.splice(proIndex, 1);
    },
    /** ********统一方法******** */ 
    keydownListener(event) {
        var self = this
      if (event.key === 'Enter') {
          self.drawer = !self.drawer
        //   if (self.runing == false) {
        //       self.handleStart()
        //   }else {
        //       self.handleEnd()
        //   }
      }
    },
    handleStart() {
        this.nowRunningSize = this.ruleForm.runningSize
        if (this.tabsActiveName == 'money') {
            this.$refs['moneyForm'].validate((valid) => {
                if (valid == false) {
                    // 校验错误
                    this.drawer = true
                } else {
                    this.nowRunningSize = this.moneyForm.runningSize
                    this.runing = true
                    this.timerStart()
                }
            })
        }else if (this.tabsActiveName == 'baowu') {
            this.$refs['baowuForm'].validate((valid) => {
                if (valid == false) {
                    // 校验错误
                    this.drawer = true
                } else {
                    this.nowRunningSize = this.baowuForm.runningSize
                    this.runing = true
                    this.timerStart()
                }
            })
        }
    },
    handleTabsClick() {

    },
    handleEnd() {
        this.timerEnd()
        this.runing = false
        this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 运行结束')
    },

    // 定时器结束
    timerEnd() {
      if (this.timer) {
          clearInterval(this.timer) // 清除定时器
          this.timer = null
        }
    },

    // 定时器运行
    timerStart() {
      this.timerEnd()
      this.startRuning()
      var self = this
      if (this.ruleForm.isRandom) {
          this.timer = setInterval(() => {
                setTimeout(() => {
                    self.startRuning()
                }, Math.floor(Math.random()*(this.nowRunningSize * 0.5)));
            }, this.nowRunningSize * 0.8)
      }else {
          this.timer = setInterval(() => {
                self.startRuning()
            }, this.nowRunningSize)
      }
    },

    startRuning() {
      this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 开始运行...')
          if (this.goodsData == null || this.goodsData.id != this.itemId) {
            this.orderId = null
          }
          if (this.orderId != null) {
            this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 检测到订单，当前订单为：'+ this.orderId +',开始付款💰')
            this.handleRequestPay()
          }else {
            if (this.tabsActiveName == 'money') {
                this.putawayAllGoodsInfo(this.tabsActiveName, function() {
                    this.handleRequestOrderId()
                }.bind(this))
            }else if (this.tabsActiveName == 'baowu') {
                this.expectAllGoodsInfo(this.tabsActiveName)
            }
          }
    },

    // 立即支付
    payStart() {
      this.timerEnd()
      this.timer = setInterval(() => {
          this.payRuning()
      }, 500)
    },

    payRuning() {
      this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 开始运行...')
          if (this.goodsData == null || this.goodsData.id != this.itemId) {
            this.orderId = null
          }
          if (this.orderId != null) {
            this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 检测到订单，当前订单为：'+ this.orderId +',开始付款💰')
            this.handleRequestPay()
          }else {
            if (this.ruleForm.roleId.length > 0) {
                this.findSellingGoods()
            }else {
                this.countdownTimerEnd()
                this.timerStart()
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 未检测到支付物品，重新检测')
            }
          }
    },

    // 倒计时
    createCountdownTimer(intDiff){
      this.countdownTimerEnd()
      var self = this
      this.countdownTimer = window.setInterval(function(){
      var day=0,
        hour=0,
        minute=0,
        second=0;//时间默认值		
      if(intDiff > 0){
        day = Math.floor(intDiff / (60 * 60 * 24));
        hour = Math.floor(intDiff / (60 * 60)) - (day * 24);
        minute = Math.floor(intDiff / 60) - (day * 24 * 60) - (hour * 60);
        second = Math.floor(intDiff) - (day * 24 * 60 * 60) - (hour * 60 * 60) - (minute * 60);
        if (self.runing == true && intDiff < 10 && intDiff > 7) {
          self.payStart()
        }
      }else {
        self.countdownTimerEnd()
        // 倒计时结束
      }
      if (minute <= 9) minute = '0' + minute;
      if (second <= 9) second = '0' + second;
      $('#day_show').html(day+"天");
      $('#hour_show').html('<s id="h"></s>'+hour+'时');
      $('#minute_show').html('<s></s>'+minute+'分');
      $('#second_show').html('<s></s>'+second+'秒');
      intDiff--;
      }, 1000);
    },
    // 结束倒计时
    countdownTimerEnd() {
      if (this.countdownTimer) {
        clearInterval(this.countdownTimer) // 清除定时器
        this.countdownTimer = null
      }
    },

    // 解析物品信息
    buildGoodsItme(typeName, pageData) {
        if (pageData.length > 0) {
            for (let index = 0; index < pageData.length; index++) {
                var goodsData = pageData[index];
                if (typeName == 'money') {
                    if (this.buildMoneyItem(goodsData) == true) {
                        this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 物品信息获取成功，物品名称：' + this.goodsData.itemName)
                        this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 检测物品已上架，可以购买')
                        return true
                    }
                }else if (typeName == 'baowu') {
                    if (this.buildBaowuItem(goodsData) == true) {
                        this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 物品信息获取成功，物品名称：' + this.goodsData.itemName)
                        this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 正在公示期，上架时间：' + this.goodsData.publicityEndDate)
                        this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']' + ' 公示期剩余时间：' + this.goodsData.remarnTime)
                        var timeDate = this.goodsData.publicityEndDate;
                        var Time = new Date(timeDate);
                        var timestemp = Time.getTime();
                        var nowTimestemp = new Date().getTime()
                        var remainTime = (timestemp - nowTimestemp) * 0.001
                        this.createCountdownTimer(remainTime)
                        return true
                    }
                }
            }
        }
        return false
    },

    // 解析银子
    buildMoneyItem(goodsData) {
        if (Number(this.moneyForm.maxPrice) >= goodsData.unitPrice && Number(this.moneyForm.maxMoney) >= goodsData.minPrice) {
            this.goodsData = goodsData
            this.ruleForm.roleId = this.goodsData.id
            return true
        }
        return false
    },
    // 解析宝物
    buildBaowuItem(goodsData) {
        var baowuTypeList = this.baowuForm.baowuTypeList
        var baowuTypeFlag = false
        for (let index = 0; index < baowuTypeList.length; index++) {
            const baowuType = baowuTypeList[index];
            if (goodsData.itemDesc.indexOf(baowuType) != -1) {
                baowuTypeFlag = true
            }
        }

        if (baowuTypeFlag == false) {
            return false
        }
        
        var baowuPorList = this.baowuFn(goodsData.itemDesc)
        for (let index = 0; index < this.baowuForm.baowuProArr.length; index++) {
            const baowuProItem = this.baowuForm.baowuProArr[index];
            var baowuPro = baowuProItem.baowuPro
            var conResult = false
            baowuPorList.map((val)=>{
                if (val.indexOf(baowuPro.name) != -1) {
                    var replaceStr = val
                    replaceStr = replaceStr.replace(baowuPro.name, '')
                    replaceStr = replaceStr.replace('>', '')
                    replaceStr = replaceStr.replace('</', '')
                    replaceStr = replaceStr.replace('+', '')
                    replaceStr = replaceStr.replace('%', '')
                    if (Number(baowuProItem.baowuProText) <= Number(replaceStr)) {
                        conResult = true
                    }
                    
                }
            })
            if (conResult == false) {
                return false
            }
        }
        this.goodsData = goodsData
        this.ruleForm.roleId = this.goodsData.id
        return true
    },

    // 获取宝物信息数据
    baowuFn(str){
      var patt1=/\>.[\u2E80-\u9FFF]*\+.[0-9]*\%\<\/|\>.[\u2E80-\u9FFF]*\+.[0-9]*\<\//g;
      var arr=str.match(patt1);
      return arr
       //将数组生成字符串
    },

