    // 根据订单号生产字符串
    getStringByOrderId(orderId) {
      var orderIdStr = orderId + ''
      var string = ''
      if (orderIdStr.length == 7) {
        for (let index = 0; index < orderIdStr.length; index++) {
          const subStr = Number(orderIdStr.substr(orderIdStr.length - index,1))
          if (orderIdStr.length - index == 6) { // 个位
            string += this.unitArr[subStr]
          }else if (orderIdStr.length - index == 5) { // 十位 
            string += this.tenArr[subStr]
          }else if (orderIdStr.length - index == 4) { // 百位 
          const subStr4 = Number(orderIdStr.substr(orderIdStr.length - 4,1))
            if (subStr4 <= 3) {
              string += this.baiArr23[subStr]
            }else {
              string += this.baiArr45[subStr]
            }
          }else if (orderIdStr.length - index == 3) { // 千位 
            string += this.qianArr[subStr]
          }else if (orderIdStr.length - index == 2) { // 万位 
            string += this.wanArr[subStr]
          }
        }
      }
      return string
    },
    fn(str){
      var patt1=/\>.[\u2E80-\u9FFF]*\<\//g;
      var arr=str.match(patt1);
      var patt2=/[\u4e00-\u9fa5]/g;
      var arr2=arr[3].match(patt2);
      return arr2.join('')       //将数组生成字符串
    },
    messageScollrBottom(message) {
      this.runingMessage += message
      setTimeout(() => {
        var scrollHeight = this.$refs.runingLog.scrollHeight
        this.$refs.runingLog.scrollTop = scrollHeight;
      });
    },

    getUrlParameter(url,name) {
    name = name.replace(/[]/, "\[").replace(/[]/, "\[").replace(/[]/, "\\\]");
    var regexS = "[\\?&]" + name + "=([^&#]*)";
    var regex = new RegExp(regexS);
    var results = regex.exec(url);
    if (results == null) return "";
    else {
      return decodeURI(results[1]);
    }
  },
    // 设置cookie
    setCookie(c_name, c_pwd, isChecked, exdays) {
      var exdate = new Date() // 获取时间
      exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays) // 保存的天数
      console.log(exdate.toGMTString(), exdate)
      // 字符串拼接cookie
      window.document.cookie = this.template_admin_userName + '=' + c_name + ';path=/;expires=' + exdate.toGMTString()
      window.document.cookie = this.template_admin_userPwd + '=' + c_pwd + ';path=/;expires=' + exdate.toGMTString()
      window.document.cookie = this.template_admin_checked + '=' + isChecked + ';path=/;expires=' + exdate.toGMTString()
    },
    // 读取cookie
    getCookie() {
      if (document.cookie.length > 0) {
        var arr = document.cookie.split('; ') // 这里显示的格式需要切割一下自己可输出看下
        for (var i = 0; i < arr.length; i++) {
          var arr2 = arr[i].split('=') // 再次切割
          // 判断查找相对应的值
          if (arr2[0] == this.template_admin_userName) {
            this.loginForm.loginName = arr2[1] // 保存到保存数据的地方
          } else if (arr2[0] == this.template_admin_userPwd) {
            this.loginForm.pwd = arr2[1]
          } else if (arr2[0] == this.template_admin_checked) {
            this.checked = true
          }
        }
      }
    },

    // 读取cookie
    getCookies(key) {
      if (document.cookie.length > 0) {
        var arr = document.cookie.split('; ') // 这里显示的格式需要切割一下自己可输出看下
        for (var i = 0; i < arr.length; i++) {
          var arr2 = arr[i].split('=') // 再次切割
          // 判断查找相对应的值
          if (arr2[0] == key) {
            return arr2[1]
          }
        }
      }
    },
    // 清除cookie
    clearCookie() {
      this.setCookie('', '', '', -1) // 修改2值都为空，天数为负1天就好了
    },
    async refreshCode() {
      console.log('checkCodeUrl', this.checkCodeUrl)
      this.picSRC = request.baseURL + request.getLoginCode + '?t=' + new Date().getTime()
    },
    getNowDate (fmt) { // author: meizz
        var nowDate = new Date()
        var o = {
            "M+": nowDate.getMonth() + 1, // 月份
            "d+": nowDate.getDate(), // 日
            "h+": nowDate.getHours(), // 小时
            "m+": nowDate.getMinutes(), // 分
            "s+": nowDate.getSeconds(), // 秒
            "q+": Math.floor((nowDate.getMonth() + 3) / 3), // 季度
            "S": nowDate.getMilliseconds() // 毫秒
        };
        if (/(y+)/.test(fmt))
            fmt = fmt.replace(RegExp.$1, (nowDate.getFullYear() + "").substr(4 - RegExp.$1.length));
        for (var k in o)
            if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
            return fmt;
    }
  }
}
</script>
