import './assets/css/tooltip-curved.css'
import {getToken, setToken} from './utils/auth'

var token = '';

try {
    var query = window.location.href;
    var token = query.split("?")[1].split('=')[1];
    token = token.indexOf('#/') > -1 ? token.split('#/')[0] : token.indexOf('/') > -1 ? token.split('/')[0] : token
    setToken(token)
} catch (e) {
    token = getToken()
}

