// ajax请求组件
let cancel;
let promiseArr = {};
const CancelToken = axios.CancelToken;
// 测试服务
// axios.defaults.baseURL = request.baseURL;

// 请求拦截器
axios.interceptors.request.use((config) => {
    // 发起请求时，取消掉当前正在进行的相同请求
    if (promiseArr[config.url]) {
        promiseArr[config.url]('操作取消');
        promiseArr[config.url] = cancel;
    } else {
        promiseArr[config.url] = cancel;
    }
    return config;
}, (error) => Promise.reject(error));

// 响应拦截器即异常处理
// eslint-disable-next-line complexity
axios.interceptors.response.use((response) => response, (err) => {
    if (err && err.response) {
        switch (err.response.status) {
            case 400:
                console.log('错误请求');
                break;
            case 401:
                // this.$alert('未授权，请重新登录', '提示信息');
                console.log('未授权，请重新登录');
                break;
            case 403:
                // this.$alert('拒绝访问', '提示信息');
                console.log('拒绝访问');
                break;
            case 404:
                // this.$alert('请求错误,未找到该资源', '提示信息');
                console.log('请求错误,未找到该资源');
                break;
            case 405:
                // this.$alert('请求方法未允许', '提示信息');
                console.log('请求方法未允许');
                break;
            case 408:
                // this.$alert('请求超时', '提示信息');
                console.log('请求超时');
                break;
            case 500:
                // this.$alert('服务器端出错', '提示信息');
                console.log('服务器端出错');
                break;
            case 501:
                // this.$alert('网络未实现', '提示信息');
                console.log('网络未实现');
                break;
            case 502:
                // this.$alert('网络错误', '提示信息');
                console.log('网络错误');
                break;
            case 503:
                // this.$alert('服务不可用', '提示信息');
                console.log('服务不可用');
                break;
            case 504:
                // this.$alert('网络超时', '提示信息');
                console.log('网络超时');
                break;
            case 505:
                // this.$alert('http版本不支持该请求', '提示信息');
                console.log('http版本不支持该请求');
                break;
            default:
                console.log(`连接错误${err.response.status}`);
        }
    } else {

        err.message = '连接到服务器失败';

        // vueApp.$router.push('/404');
        // alert('连接到服务器失败', '提示信息');
    }
    console.log(err.message);
    return Promise.resolve(err);
});

// 设置默认请求头
axios.defaults.headers = {
    'Content-Type': 'application/json',
    'X-AUTH-TOKEN': token,
};
axios.defaults.timeout = 1000000;

// get请求 = (name) => {
Vue.prototype.$get = (url, param) => new Promise((resolve, reject) => {
    axios({
        method: 'get',
        url,
        params: param,
        cancelToken: new CancelToken((c) => {
            cancel = c;
        }),
    }).then((res) => {
        console.log(res, '登录');
        resolve(res);
        if (res.data !== undefined && res.data.code === -1009) {
            loginErr();
            return false;
        }
    });
});
// post请求
Vue.prototype.$post = (url, param) => new Promise((resolve, reject) => {
    axios({
        method: 'post',
        url,
        data: param,
        cancelToken: new CancelToken((c) => {
            cancel = c;
        }),
    }).then((res) => {
        if (res !== undefined) {
            resolve(res);
            if (res.data !== undefined && res.data.code === -1009) {
                loginErr();
                return false;
            }
        }
    });
});

function loginErr() {
    ElementUI.MessageBox.confirm('您的账号已在其它地方登录 ！', '信息提示！', {
        distinguishCancelAndClose: true,
        confirmButtonText: '确定',
        cancelButtonText: '重新登录',
        center: true,
    })
        .then(() => {
            router.push('/login')
        })
        ['catch']((action) => {
        router.push('/login')
    });
    return false;
}


Vue.config.productionTip = false;
Vue.use(ElementUI);

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
