              <strong id="minute_show">0分</strong>
              <strong id="second_show">0秒</strong>
          </div><!--倒计时模块-->
        </div>
        <div v-if="goodsData != null" @click="drawer = true" class="goods_details">
          <div slot="reference" class="goods_block tooltip ">
              <div class="goods_info open_goods">
                  <div class="goods_img tag_box">
                      <img :src="goodsData.iconPath">
                      <div class="tags">
                      </div>
                  </div>
