                        <dd class="goods_amount"><label>数量:</label>{{goodsData.itemAmount}}</dd>
                        <dd class="goods_amount"><label>关注:</label>{{followCount}}</dd>
                      </div>
                      <dd class="goods_price">¥{{goodsData.price}}</dd>
