              </div>
              <div v-if="goodsData.itemDesc != null && goodsData.itemDesc.length > 0" class="tooltip-content" v-html="goodsData.itemDesc"></div>
              <!-- <div v-else class="reolesDesc tooltip-content"><span>{{goodsData.itemName}}<br>{{'性别:' + goodsData.gender}}<br>{{'等级:' + goodsData.grade}}<br>{{'门派:' + goodsData.guild}}<br></span><img align="right" width="90" style="background-color:#2C281E" height="90" :src="goodsData.iconPath"></div> -->
              <div v-show="goodsData.itemType == '3000'" v-else class="reolesDesc tooltip-content">
                <div style="width:250px; padding-left:6px; padding-top:6px; padding-bottom:6px;">
                  <font style="font-size:14px" >{{goodsData.itemName}}</font><br>
                  <br>
                  性别:
