                  <br>
                  <div style="float: left; z-index:98; position:absolute; left: 150px; top: 16px; border:1px solid #333;"  valign="top;" >
                    <img width="90" height="90" align="top"  :src="goodsData.iconPath"/>
                  </div>
                </div>
                </div>
              <!-- <br>{{'性别:' + goodsData.gender}}<br>{{'等级:' + goodsData.grade}}<br>{{'门派:' + goodsData.guild}}<br> -->
            </div>
        </div>
        <div class="sub-btn">
            <!-- <el-button v-if="runing == false"  @click="handleStart()" type="primary">{{runing ? '运行中' : '开始运行'}}</el-button> -->
            <el-button type="primary" v-if="runing == false"  @click="handleStart()" icon="el-icon-thumb" circle></el-button>
            <el-button v-if="runing" @click="handleEnd()" type="primary" icon="el-icon-switch-button" circle></el-button>
        </div>
    </div>

      <div v-if="runing" class="wrap">
