                v-model.trim="ruleForm.roleId"
                placeholder="请输入物品ID"
                />
            </el-form-item>
            <el-form-item label="运行频率（毫秒）：" prop="runningSize">
                <el-input-number v-model="ruleForm.runningSize" :step="100" :min="0" :max="5000"></el-input-number>
            </el-form-item>

           </el-form>
        </div>
