</template>
<script>
  // import md5 from "js-md5"
import Cookies from 'js-cookie'
import { setToken } from '../utils/auth'
import axios from 'axios'
import request from '../utils/urls'
import $ from 'jquery'
export default {
  name: 'Login',
  data() {
      return {
        drawer: false,
        direction: 'rtl',
        runing: false,
        itemId: null,
        orderId: null,
        followCount: '',
        runingMessage: '',
        timer: null,
        countdownTimer: null,
        goodsData: null,
        isRequestOrderId: false,
        nowRunningSize: 50, // 当前的运行频率
        ruleForm: {
            loginCookie: '', 
            roleId: '',
            runningSize: 50
        },
        rules: {
          loginCookie: [
            { required: true, message: '请输入Cookie', trigger: 'change' }
          ],
          roleId: [
            { required: true, message: '请输入物品ID', trigger: 'change' },
            { min: 7, max: 7, message: '物品ID支持7个字符长度', trigger: 'change' },
            {
              pattern: /^[0-9]{7}$/,
              message: '物品ID必须为数字'
            }
          ]
        },
        unitArr:[   // 个位
            'nh',
            'nl',
            'np',
            'nt',
            'jF',
            'jJ',
            'jN',
            'jR',
            'jV',
            'jZ'
        ],
        tenArr:[  // 十位
            'B',
            'F',
            'J',
            'N',
            'R',
            'V',
            'Z',
            'j',
            'n',
            'r'
        ],
        qianArr:[  // 千位
            '4',
            '5',
            '6',
            '7',
            'x',
            'y',
            'z',
            '0',
            '1',
            '2'
        ],
        wanArr:[  // 万位
            'QX',
            'RX',
            'SX',
            'TX',
            'UX',
            'VX',
            'WX',
            'XX',
            'YX',
            'a2'
        ],
        baiArr23:[  // 百位
            'RU1',
            'VU1',
            'a01',
            'e01',
            'RU5',
            'VU5',
            'a05',
            'e05',
            'RU9',
            'VU9'
        ],
        baiArr45:[  // 百位
            'RU0',
            'VU0',
            'a00',
            'e00',
            'RU4',
            'VU4',
            'a04',
            'e04',
            'RU8',
            'VU8'
        ]
      }
    },
  watch:{
    drawer(val) {
      if (this.drawer == false) {
        this.$refs['ruleForm'].validate((valid) => {
          if (valid) {
            this.expectGoodsInfo()
          }else {
            this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 物品ID不合法，请重新输入')
            this.goodsData = null
          }
        })
        this.countdownTimerEnd()
      }
    }
  },
  mounted() {
    document.addEventListener('keydown', this.keydownListener)
    this.drawer = true
    this.messageScollrBottom('[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 欢迎使用～～👏👏👏')
  },
  beforeDestroy() {
    document.removeEventListener('keydown', this.keydownListener)
  },
  methods: {
    keydownListener(event) {
        var self = this
      if (event.key === 'Enter') {
          self.drawer = !self.drawer
          // if (self.runing == false) {
          //     self.handleStart()
          // }else {
          //     self.handleEnd()
          // }
      }
    },
    handleStart() {
      this.drawer = false
        this.nowRunningSize = this.ruleForm.runningSize
        this.$refs['ruleForm'].validate((valid) => {
        if (valid == false) {
          // 校验错误
          this.drawer = true
        } else {
          this.runing = true
          this.timerStart()
        }
      })
    },
    handleEnd() {
        this.timerEnd()
        this.runing = false
        this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 运行结束')
    },

    // 定时器结束
    timerEnd() {
      if (this.timer) {
          clearInterval(this.timer) // 清除定时器
          this.timer = null
        }
    },

    // 定时器运行
    timerStart() {
      this.timerEnd()
      this.timer = setInterval(() => {
          this.startRuning()
      }, this.nowRunningSize)
    },

    startRuning() {
      this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 开始运行...')
          if (this.ruleForm.roleId != this.itemId) {
            this.orderId = null
          }
          if (this.orderId != null) {
            this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 检测到订单，当前订单为：'+ this.orderId +',开始付款💰')
            this.handleRequestPay()
          }else {
            this.findExpectGoods()
          }
    },

    // 倒计时
    createCountdownTimer(intDiff){
      this.countdownTimerEnd()
      var self = this
      this.countdownTimer = window.setInterval(function(){
      var day=0,
        hour=0,
        minute=0,
        second=0;//时间默认值		
      if(intDiff > 0){
        day = Math.floor(intDiff / (60 * 60 * 24));
        hour = Math.floor(intDiff / (60 * 60)) - (day * 24);
        minute = Math.floor(intDiff / 60) - (day * 24 * 60) - (hour * 60);
        second = Math.floor(intDiff) - (day * 24 * 60 * 60) - (hour * 60 * 60) - (minute * 60);
        if (self.runing == true && intDiff < 5 && intDiff > 2) {
          self.timerStart()
        }
      }else {
        self.countdownTimerEnd()
        // 倒计时结束
      }
      if (minute <= 9) minute = '0' + minute;
      if (second <= 9) second = '0' + second;
      $('#day_show').html(day+"天");
      $('#hour_show').html('<s id="h"></s>'+hour+'时');
      $('#minute_show').html('<s></s>'+minute+'分');
      $('#second_show').html('<s></s>'+second+'秒');
      intDiff--;
      }, 1000);
    },
    // 结束倒计时
    countdownTimerEnd() {
      if (this.countdownTimer) {
        clearInterval(this.countdownTimer) // 清除定时器
        this.countdownTimer = null
      }
    },

    // 公示期获取物品信息 // 普通检查信息
    async expectGoodsInfo() {
      this.getFollowCount()
      var url = 'http://jishi.woniu.com/9yin/findNoticeGoods.do?&pageIndex=1&id='+ this.ruleForm.roleId +'&_=' + (new Date()).getTime()
      this.$get(url)
            .then((response) => {
              if (response.data == null) {
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 网络请求失败，请稍后再试～～')
                return
              }
            if (response.data.indexOf('点击登录') != -1) {
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 登录过期，请重新设置Cookie，运行结束')
                window.open("http://jishi.woniu.com/resources/9yin/toServerList.html", "_blank");
                this.handleEnd()
                return
            }
            if (response.data.length > 0) { // 在公示期中
              var data = response.data[0]
              if (data.pageData.length > 0) {
                this.goodsData = data.pageData[0]
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 物品信息获取成功，物品名称：' + this.goodsData.itemName)
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 正在公示期，上架时间：' + this.goodsData.publicityEndDate)
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 公示期剩余时间：' + this.goodsData.remarnTime)
                var timeDate = this.goodsData.publicityEndDate;
                timeDate = timeDate.replace(/-/g,'/')
                var Time = new Date(timeDate);
                var timestemp = Time.getTime();
                var nowTimestemp = new Date().getTime()
                var remainTime = (timestemp - nowTimestemp) * 0.001
                this.createCountdownTimer(remainTime)
                // this.timerEnd() //结束定时器
                // this.timer = setInterval(() => {
                //   this.expectGoodsInfo()
                // }, 60000) // 将定时器改成60秒请求一次 防止退出
                return
              }
            }
            this.putawayGoodsInfo()
            }).catch((err) => {
              this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 系统错误停止运行：' + err)
              this.handleEnd()
            })
    },

    // 获取上架物品信息
    async putawayGoodsInfo() {
      var url = 'http://jishi.woniu.com/9yin/findSellingGoods.do?pageIndex=1&id='+ this.ruleForm.roleId +'&_=' + (new Date()).getTime()
      this.$get(url)
            .then((response) => {
              if (response.data == null) {
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 网络请求失败，请稍后再试～～')
                return
              }
            if (response.data.indexOf('点击登录') != -1) {
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 登录过期，请重新设置Cookie，运行结束')
                window.open("http://jishi.woniu.com/resources/9yin/toServerList.html", "_blank");
                this.handleEnd()
                return
            }
            if (response.data.length > 0) { // 已上架
              var data = response.data[0]
              if (data.pageData.length > 0) {
                this.goodsData = data.pageData[0]
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 物品信息获取成功，物品名称：' + this.goodsData.itemName)
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 检测物品已上架，可以购买')
                return
              }
              this.auctionGoodsInfo()
            }
            }).catch((err) => {
              this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 系统错误停止运行：' + err)
              this.handleEnd()
            })
      
    },

    // 获取拍卖行物品
    async auctionGoodsInfo() {
      var url = 'http://jishi.woniu.com/9yin/findAuctionGoods.do?pageIndex=1&id='+ this.ruleForm.roleId +'&_=' + (new Date()).getTime()
      this.$get(url)
            .then((response) => {
              if (response.data == null) {
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 网络请求失败，请稍后再试～～')
                return
              }
            if (response.data.indexOf('点击登录') != -1) {
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 登录过期，请重新设置Cookie，运行结束')
                window.open("http://jishi.woniu.com/resources/9yin/toServerList.html", "_blank");
                this.handleEnd()
                return
            }
            if (response.data.length > 0) { // 已上架
              var data = response.data[0]
              if (data.pageData.length > 0) {
                this.goodsData = data.pageData[0]
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 物品信息获取成功，物品名称：' + this.goodsData.itemName)
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 检测物品正在拍卖中')
                return
              }
              this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 没有检测到相应的物品，请核对物品ID是否正确')
              this.goodsData = null
            }
            }).catch((err) => {
              this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 系统错误停止运行：' + err)
              this.handleEnd()
            })
      
    },

    // 获取关注数量
    async getFollowCount() {
      var url = 'http://jishi.woniu.com/9yin/getItemsFollowCount.do?itemIds='+ this.ruleForm.roleId +'&_=' + (new Date()).getTime()
      this.$get(url)
            .then((response) => {
              if (response.data != null && response.data.length > 0) { 
                var data = response.data[0]
                this.followCount = data.data[this.ruleForm.roleId]
              }
            }).catch((err) => {
              this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 请求物品关注数量错误：' + err)
            })
    },

    // 先判断是否上架
    async findSellingGoods() {
      var url = 'http://jishi.woniu.com/9yin/findSellingGoods.do?pageIndex=1&id='+ this.ruleForm.roleId +'&_=' + (new Date()).getTime()
      this.$get(url)
            .then((response) => {
              if (response.data == null) {
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 网络请求失败，请稍后再试～～')
                return
              }
            if (response.data.indexOf('点击登录') != -1) {
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 登录过期，请重新设置Cookie，运行结束')
                window.open("http://jishi.woniu.com/resources/9yin/toServerList.html", "_blank");
                this.handleEnd()
                return
            }
            if (response.data.length > 0) {
              var data = response.data[0]
              if (data.pageData.length > 0) {
                this.goodsData = data.pageData[0]
              // 已上架
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 检测到物品已上架，物品名称：' + data.pageData[0].itemName)
                this.handleRequestOrderId()
                return
              }
            }
            this.findExpectGoods()
            }).catch((err) => {
              this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 系统错误停止运行：' + err)
              this.handleEnd()
            })
    },

    // 公示期获取物品信息
    async findExpectGoods() {
      // if (this.goodsData) {
      //     var timeDate = this.goodsData.publicityEndDate;
      //     timeDate = timeDate.replace(/-/g,'/')
      //     var Time = new Date(timeDate);
      //     var timestemp = Time.getTime();
      //     var nowTimestemp = new Date().getTime()
      //     if (nowTimestemp >= timestemp + 20) {
      //       this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 时间已到')
      //       this.handleRequestOrderId()
      //     }
      // }
      var url = 'http://jishi.woniu.com/9yin/findNoticeGoods.do?&pageIndex=1&id='+ this.ruleForm.roleId +'&_=' + (new Date()).getTime()
      var requestTime = (new Date()).getTime()
      this.$get(url)
            .then((response) => {
              if (response.data == null) {
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 网络请求失败，请稍后再试～～')
                return
              }
              if (response.data.indexOf('点击登录') != -1) {
                  this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 登录过期，请重新设置Cookie，运行结束')
                  window.open("http://jishi.woniu.com/resources/9yin/toServerList.html", "_blank");
                  this.handleEnd()
                  return
              }
              if (response.data.length > 0) { // 在公示期中
                var data = response.data[0]
                if (data.pageData.length > 0) {
                  this.goodsData = data.pageData[0]
                  this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 检测物品正在公示期，物品名称：' + data.pageData[0].itemName)
                  this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 预计上架时间：' + data.pageData[0].publicityEndDate)
                  this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 公示期剩余时间：' + data.pageData[0].remarnTime)
                  var timeDate = this.goodsData.publicityEndDate;
                  timeDate = timeDate.replace(/-/g,'/')
                  var Time = new Date(timeDate);
                  var timestemp = Time.getTime();
                  var nowTimestemp = new Date().getTime()
                  var remainTime = (timestemp - nowTimestemp) * 0.001
                  var remainTimeT = timestemp - nowTimestemp
                  var jetTime = nowTimestemp - requestTime
                  if (this.ruleForm.runningSize > jetTime) {
                    this.ruleForm.runningSize = jetTime
                  }
                  var jetTimeFloat = this.ruleForm.runningSize * 0.001
                  if (remainTime >= 5) {
                    this.createCountdownTimer(remainTime)
                    this.timerEnd() //结束定时器
                    this.timer = setInterval(() => {
                      this.startRuning() 
                    }, 30000) // 将定时器改成30秒请求一次 防止退出
                  }
                  // if (remainTimeT <= this.ruleForm.runningSize) {
                  //   var self = this
                  //   setTimeout(() => {
                  //     self.messageScollrBottom('<br>[' + self.getNowDate('hh:mm:ss:S') + ']:' + ' 时间已到')
                  //     self.messageScollrBottom('<br>[' + self.getNowDate('hh:mm:ss:S') + ']:' + ' 开始创建订单...')
                  //     self.handleRequestOrderId()
