                 this.orderId = data.orderId
                 this.itemId = this.ruleForm.roleId
                 this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 创建订单成功，订单号为：' + this.orderId + '--->' + currentTime)
                 this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 当前订单为：'+ this.orderId +',开始付款💰')
