                 this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 订单创建失败，原因：' + data.msg + '--->' + currentTime)
                 if (data.msg.indexOf('请稍后再试') == -1) {
                    this.handleEnd()
                 }
              }
            }
              
            }).catch((err) => {
              this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + ' 系统错误停止运行：' + err)
              this.handleEnd()
            })
    },
    // 获取答案
    async handleRequestAnswer() {
