    // 付款
    async handleRequestPay() {
      this.handleRequestAnswer()
      // 前缀
      var pr_url = 'http://jishi.woniu.com/9yin/invokePay.do?p=PndYYjFobkxtZFhZUjFuY3FabmJ3TmxjbVJuY3c5amJxbFlPd01ZWmtKWWR3TllaejlUYndObkwybG5id2RvTHFoM2NxcDNMd29FYzFSSWE6VTNZcVpvY21Ob0p5MWtjbWQ0Y3ZGbkp5RlljeUZZYzpRbmN3ZDRje0ZIVTZGSGNuQVVQdUo0Ym5SWVl0QldlaUJvS'
      var suffix_url = 'tNMjBFWktKWVpsSjRi'
      var center_url = this.getStringByOrderId(this.orderId)
      // 加密
      var requestUrl = pr_url + center_url + suffix_url +'&_=' + (new Date()).getTime()
      var currentTime = (new Date()).getTime()
      this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + '===>'+ '付款开始：' + currentTime)
      this.$get(requestUrl)
            .then((response) => {
              this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + '===>'+ '付款结束：' + currentTime)
              if (response.data == null) {
                this.messageScollrBottom('<br>[' + this.getNowDate('hh:mm:ss:S') + ']:' + '购买失败,原因：网络请求失败，请稍后再试～～')
                return
